import React from 'react';
import styles from './MySpecInven.scss';
import classNames from 'classnames/bind';
import Radio from 'components/common/Radio';
import Adsense320100 from 'components/adsense/Adsense320100';

const cx = classNames.bind(styles);

const MySpecInven = ({myStatInsert, myStat, setParaDoping, inputClick, skillList, stype1}) => {
  const {conStat, skillStat, itemDmgUp, limitDmg, itemCriUp, isParasite, whereDoping, monsterSize, monsterType, myDmgType, currSkillSeq, dmgEvent} = myStat;
  const stypeDisabled = stype1 === '2' ? true : false;
  const mySkillList = skillList.map((skill) => {
    const {seq, name, increaseTarget, increaseRt} = skill;

    return (
      <div key={seq}>
        <Radio name='currSkillSeq' value={seq} onChange={setParaDoping} defaultValue={currSkillSeq}>
          {name}({increaseTarget === 'all' ? '' : seq === '14' ? '' : '' } {seq === '14' ? '1.5' : increaseRt}{seq === '14' ? '(3)' : ''})
        </Radio>
      </div>
    );
  });
  
  return (
    <div className={cx('myspec-inven')}>
      <div className={cx('myspec-body')}>
        <h2 className={cx('myspec-title')}></h2>
        <ul>
          <li><b></b> :   (28+6+6=40)</li>
          <li><b></b> :   (12+20=32)</li>
          <li><b></b> :  </li>
        </ul>
        <div className={cx('spec-dv')}>
          <div className={cx('mystat')}>
            <div>
              <div className={cx('mystat-con')}></div>
              <div><input type='number' name='conStat' value={conStat} onChange={myStatInsert} onClick={inputClick}/></div>
            </div>
            <div>
              <div className={cx('mystat-skill')}></div>
              <div><input type='number' name='skillStat' value={skillStat} onChange={myStatInsert} onClick={inputClick}/></div>
            </div>
            <div>
              <div></div>
              <div><input type='number' name='itemDmgUp' value={itemDmgUp} onChange={myStatInsert} onClick={inputClick}/></div>
            </div>
            <div>
              <div></div>
              <div><input type='number' name='itemCriUp' value={itemCriUp} onChange={myStatInsert} onClick={inputClick}/></div>
            </div>
            <div>
              <div></div>
              <div><input type='number' name='limitDmg' value={limitDmg} onChange={myStatInsert} onClick={inputClick}/></div>
            </div>
          </div>
          <div className={cx('parasite-doping')}>
            <div className={cx('parasite')}>
              <div><Radio name='isParasite' value='0' onChange={setParaDoping} defaultValue={isParasite}></Radio></div>
              <div><Radio name='isParasite' value='2.5' onChange={setParaDoping} defaultValue={isParasite}>1</Radio></div>
              <div><Radio name='isParasite' value='3' onChange={setParaDoping} defaultValue={isParasite}></Radio></div>
            </div>
            <div className={cx('doping')}>
              <div className={cx('whereDoping')}>
                <div><Radio name='whereDoping' value='0' onChange={setParaDoping} defaultValue={whereDoping}></Radio></div>
                <div><Radio name='whereDoping' value='2' onChange={setParaDoping} defaultValue={whereDoping}></Radio></div>
                <div><Radio name='whereDoping' value='3' onChange={setParaDoping} defaultValue={whereDoping}></Radio></div>
              </div>
            </div>
          </div>
        </div>
        <Adsense320100/>
      </div>
      <div className={cx('skill-monster')}>
        <h2 className={cx('monster-size-title')}> </h2>
        <div className={cx('monster-size')}>
        <div><Radio name='monsterSize' value='' onChange={setParaDoping} defaultValue={monsterSize}></Radio></div>
          <div><Radio name='monsterSize' value='' onChange={setParaDoping} defaultValue={monsterSize}></Radio></div>
          <div><Radio name='monsterSize' value='' onChange={setParaDoping} defaultValue={monsterSize}></Radio></div>
        </div>
        <div className={cx('monster-type')}>
          <div><Radio name='monsterType' value='' onChange={setParaDoping} defaultValue={monsterType}></Radio></div>
          <div><Radio name='monsterType' value='' onChange={setParaDoping} defaultValue={monsterType} disabled={stypeDisabled}></Radio></div>
          <div><Radio name='monsterType' value='' onChange={setParaDoping} defaultValue={monsterType} disabled={stypeDisabled}></Radio></div>
          <div><Radio name='monsterType' value='' onChange={setParaDoping} defaultValue={monsterType} disabled={stypeDisabled}></Radio></div>
          <div><Radio name='monsterType' value='' onChange={setParaDoping} defaultValue={monsterType} disabled={stypeDisabled}></Radio></div>
          <div><Radio name='monsterType' value='' onChange={setParaDoping} defaultValue={monsterType} disabled={stypeDisabled}></Radio></div>
        </div>
        <h2 className={cx('monster-size-title')}>( ) </h2>
        <div className={cx('mydmg-type')}>
          <div><Radio name='myDmgType' value='' onChange={setParaDoping} defaultValue={myDmgType}></Radio></div>
          <div><Radio name='myDmgType' value='' onChange={setParaDoping} defaultValue={myDmgType} disabled={stypeDisabled}></Radio></div>
          <div><Radio name='myDmgType' value='Slug' onChange={setParaDoping} defaultValue={myDmgType} disabled={stypeDisabled}>Slug</Radio></div>
          <div><Radio name='myDmgType' value='' onChange={setParaDoping} defaultValue={myDmgType} disabled={stypeDisabled}></Radio></div>
          <div><Radio name='myDmgType' value='' onChange={setParaDoping} defaultValue={myDmgType} disabled={stypeDisabled}></Radio></div>
          <div><Radio name='myDmgType' value='' onChange={setParaDoping} defaultValue={myDmgType} disabled={stypeDisabled}></Radio></div>
          <div><Radio name='myDmgType' value='' onChange={setParaDoping} defaultValue={myDmgType} disabled={stypeDisabled}></Radio></div>
          <div><Radio name='myDmgType' value='' onChange={setParaDoping} defaultValue={myDmgType} disabled={stypeDisabled}></Radio></div>
          <div><Radio name='myDmgType' value='' onChange={setParaDoping} defaultValue={myDmgType} disabled={stypeDisabled}></Radio></div>
          <div><Radio name='myDmgType' value='' onChange={setParaDoping} defaultValue={myDmgType} disabled={stypeDisabled}></Radio></div>
          <div><Radio name='myDmgType' value='' onChange={setParaDoping} defaultValue={myDmgType} disabled={stypeDisabled}></Radio></div>
        </div>
        <div className={cx('my-skill-list')}>
          <div><Radio name='currSkillSeq' value='0' onChange={setParaDoping} defaultValue={currSkillSeq}></Radio></div>
          {mySkillList}
        </div>
        <h2 className={cx('dmg-event-title')}> </h2>
        <div className={cx('dmg-event')}>
          <div><Radio name='dmgEvent' value='0' onChange={setParaDoping} defaultValue={dmgEvent}></Radio></div>
          <div><Radio name='dmgEvent' value='5' onChange={setParaDoping} defaultValue={dmgEvent}>5%</Radio></div>
          <div><Radio name='dmgEvent' value='10' onChange={setParaDoping} defaultValue={dmgEvent}>10%</Radio></div>
          <div><Radio name='dmgEvent' value='20' onChange={setParaDoping} defaultValue={dmgEvent}>20%</Radio></div>
        </div>
      </div>
    </div>
  );
};

export default MySpecInven;